                    <h4>
                      Compara
                    </h4>
  
                    <ul>
                      <li>
                        Compara el código de la imagen del acta con el proporcionado por el lector de código HASH 256.                    
                      </li>
                      <li>
                        Asegúrate de que todos los caracteres coincidan, independientemente de las mayúsculas o minúsculas.                  
                      </li>
                    </ul>
                  </div>
